<template>
  <div class="sell-wrapper" ref="sellerref">
    <div class="sellcontent">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc border-1px">
          <v-star :size="36" :score="seller.score"></v-star>
          <span class="text">({{seller.ratingCount}})</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <div class="content">
              <span class="stress">{{seller.minPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryPrice}}</span>元
            </div>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <div class="content">
              <span class="stress">{{seller.deliveryTime}}</span>分钟
            </div>
          </li>
        </ul>
        <div class="favorite" @click.stop.prevent="togglefavoriteinfo($event)">
          <span class="icon-favorite" :class="{'active':favorited}"></span>
          <span class="text">{{favoritetext}}</span>
        </div>
      </div>
      <v-Split></v-Split>
      <div class="bulletin">
        <h1 class="title">公告与活动</h1>
        <div class="content-wrapper border-1px">
          <p class="content">{{seller.bulletin}}</p>
        </div>

        <ul v-if="seller.supports" class="supports">
          <li v-for="item in seller.supports" class="support-item border-1px">
            <span class="icon" :class="classMap[item.type]"> </span>
            <span class="text">{{item.description}}</span>
          </li>
        </ul>

      </div>
      <v-Split></v-Split>
      <div class="pics">
        <h1 class="title">商家实景</h1>
        <div class="pic-wrapper" ref="picwrapperref">
          <ul class="picList" ref="picListref">
            <li class="picitem" v-for="pic in seller.pics">
              <img width="120" height="90" :src="pic">
            </li>
          </ul>
        </div>
      </div>
      <v-Split></v-Split>
      <div class="sellerinfo">
        <h1 class="title border-1px">商家信息</h1>
        <ul>
          <li class="info-item border-1px" v-for="item in seller.infos">
            {{item}}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>


<script type="text/ecmascript-6">
  import star from '../../components/star/star.vue';
  import Split from '../split/split.vue';
  import BScroll from 'better-scroll';
  import {saveToLocal, loadFromLocal} from '../../common/js/store';
  export default{
    props: {
      seller: {
        type: Object
      }
    },
    data() {
      return {
        favorited: (() => {
          return loadFromLocal(this.seller.id, 'favorite', false);
        })()
      };
    },
    computed: {
      favoritetext() {
        return this.favorited ? '已收藏' : '收藏';
      }
    },
    created() {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    watch: {
      'seller'() {
        this.$nextTick(() => {
            this._initscroll();
            this._initPics();
          }
        );
      }
    },
    mounted() {
      this.$nextTick(() => {
        this._initscroll();
        this._initPics();
      });
    },
    methods: {
      _initscroll() {
        if (!this.scroll) {
          this.scrool = new BScroll(this.$refs.sellerref, {
            click: true
          });
        } else {
          this.scrool.refresh();
        }
      },
      _initPics() {
        if (this.seller.pics) {
          let picWidth = 120;
          let margin = 6;
          let width = (picWidth + margin) * this.seller.pics.length - margin;
          this.$refs.picListref.style.width = width + 'px';

          this.$nextTick(() => {
            if (!this.picScroll) {
              this.picScroll = new BScroll(this.$refs.picwrapperref, {
                scrollX: true,
                eventPassthrough: 'vertical'
              });
            } else {
              this.picScroll.refresh();
            }
          });
        }
      },
      togglefavoriteinfo(event) {
        console.log(event);
        console.log(!this.favorited);
        console.log(this.seller);
        this.favorited = !this.favorited;
        saveToLocal(this.seller.id, 'favorite', this.favorited);
      }
    },
    components: {
      'v-star': star,
      'v-Split': Split
    }
  };
</script>


<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl";
  .sell-wrapper
    position absolute
    top 174px
    bottom 0px
    width 100%
    left: 0
    overflow hidden
    .overview
      padding 18px
      position relative
      .title
        margin-bottom 8px
        line-height 14px
        color rgb(7, 17, 27)
        font-size 14px
      .desc
        padding-bottom 18px
        font-size 0
        border-1px(rgba(7, 17, 27, 0.1))
        .star
          display inline-block
          vertical-align top
          margin-right 8px
        .text
          display inline-block
          vertical-align top
          margin-right 12px
          line-height 18px
          font-size 10px
          color rgb(77, 85, 93)
      .remark
        display flex
        padding-top 18px
        .block
          flex 1
          text-align center
          border-right 1px solid rgba(7, 17, 27, 0.1)
          &:last-child
            border none
          h2
            margin-bottom 4px
            line-height 10px
            font-size 10px
            color rgb(147, 153, 159)
          .content
            line-height 24px
            font-size 10px
            color rgb(7, 17, 27)
            .stress
              font-size 24px

      .favorite
        position absolute
        right 18px
        top: 18px
        width 50px
        text-align center
        .icon-favorite
          display block
          margin-bottom 4px
          line-height 24px
          font-size 24px
          color #d4d6d9
          &.active
            color rgb(240, 20, 20)
        .text
          line-height 10px
          font-size 10px
          color rgb(77, 85, 93)

    .bulletin
      padding 18px 18px 0 18px
      .title
        margin-bottom 8px
        line-height 14px
        color rgb(7, 17, 27)
        font-size 14px
      .content-wrapper
        padding 0 12px 16px 12px
        border-1px(rgba(7, 17, 27, 0.1))
        .content
          line-height 24px
          font-size 12px
          color rgb(240, 20, 20)
      .supports
        .support-item
          padding 16px 12px
          font-size 0
          border-1px(rgba(7, 17, 27, 0.1))
          &:last-child
            border-none()
          .icon
            display: inline-block
            vertical-align: top
            width: 16px
            height: 16px
            margin-right: 6px
            background-size: 16px 16px
            background-repeat: no-repeat
            &.decrease
              bg-image('decrease_4')
            &.discount
              bg-image('discount_4')
            &.guarantee
              bg-image('guarantee_4')
            &.invoice
              bg-image('invoice_4')
            &.special
              bg-image('special_4')
          .text
            line-height 16px
            font-size 12px
            color rgb(7, 17, 27)

    .pics
      padding 18px
      .title
        margin-bottom 6px
        line-height 14px
        color rgb(7, 17, 27)
        font-size 14px
      .pic-wrapper
        width 100%
        overflow hidden
        white-space nowrap
        font-size 0
        .picList
          font-size 0
          .picitem
            display inline-block
            margin-right 6px
            width 120px
            height 90px
            &:last-child
              margin 0

    .sellerinfo
      padding 18px 18px 0 18px
      color rgb(7, 17, 27)
      .title
        padding-bottom 6px
        line-height 14px
        font-size 14px
        border-1px(rgba(7, 17, 27, 0.1))
      .info-item
        padding 16px 12px
        line-height 16px
        border-1px(rgba(7, 17, 27, 0.1))
        font-size 12px
        &:last-child
          border-none()

</style>




