<template>
  <div class="ratings" ref="ratingsref">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家{{seller.score}}%</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <v-star :size="24" :score="seller.serviceScore"></v-star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title">商品评分</span>
            <v-star :size="24" :score="seller.foodScore"></v-star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title">送到时间</span>
            <span class="delivery">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <v-Split></v-Split>
      <v-ratingselect @toggleconent="toggleConent" @select="selectRating" :select-type="selectType"
                      :onlyContent="onlyContent"
                      :ratings="ratings"></v-ratingselect>

      <div class="ratings-wrapper">
        <ul>
          <li  v-show="needshow(rating.rateType,rating.text)" v-for="rating in ratings" class="ratings-item border-1px">
            <div class="avatar">
              <img width="28" height="28" :src="rating.avatar">
            </div>

            <div class="content">
              <div class="name">{{rating.username}}</div>
              <div class="star-wrapper">
                <v-star :size="24" :score="rating.score"></v-star>
                <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟</span>
              </div>
              <p class="text">{{rating.text }}</p>
              <div class="recomand-wrapper" v-show="rating.recommend && rating.recommend.length">
                <span class="icon-thumb_up"></span>
                <span class="item" v-for="recommend in rating.recommend">{{recommend}}</span>
              </div>
              <div class="time">{{rating.rateTime | formatDate}}</div>
            </div>

          </li>
        </ul>
      </div>
    </div>
  </div>
</template>


<script type="text/ecmascript-6">
  import BScroll from 'better-scroll';
  import star from '../../components/star/star.vue';
  import Split from '../split/split.vue';
  import ratingselect from '../ratingselect/ratingselect.vue';
  import {formatDate} from '../../common/js/date';
  const ALL = 2;
  const ERR_OK = 0;
  export default {
    props: {
      seller: {
        type: Object
      }
    },
    created() {
      this.$http.get('./api/ratings').then((response) => {
        response = response.body;
        if (response.errno === ERR_OK) {
          this.ratings = response.data;
          this.$nextTick(() => {
            this.scrool = new BScroll(this.$refs.ratingsref, {
              click: true
            });
          });
        }
      });
    },
    data() {
      return {
        ratings: [],
        selectType: ALL,
        onlyContent: true
      };
    },
    methods: {
      selectRating (type) {
          console.log('qeqweqwe');
        this.selectType = type;
        this.$nextTick(() => {
          this.scrool.refresh();
        });
      },
      toggleConent () {
        console.log('qweqe');
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this.scrool.refresh();
        });
      },
      needshow(type, content) {
        if (this.onlyContent && !content) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return this.selectType === type;
        }
      }
    },
    components: {
      'v-star': star,
      'v-Split': Split,
      'v-ratingselect': ratingselect
    },
    filters: {
      formatDate (time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
    }
  };
</script>


<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl";
  .ratings
    position absolute
    top 174px
    bottom 0px
    width 100%
    left: 0
    overflow hidden
    .overview
      display flex
      padding 18px 0
      .overview-left
        flex 0 0 137px
        width 137px
        padding 6px 0
        border-right: 1px solid rgba(7, 17, 27, 0.1)
        text-align center
        @media only screen and (max-width: 320px)
          flex: 0 0 120px
          width: 120px
        .score
          margin-bottom 6px
          line-height 28px
          font-size 24px
          color rgb(255, 153, 0)
        .title
          margin-bottom 8px
          line-height 12px
          font-size 12px
          color rgb(7, 17, 27)
        .rank
          padding-bottom 6px
          line-height 10px
          font-size 10px
          color rgb(143, 157, 159)
      .overview-right
        flex 1
        padding 6px 0 6px 24px
        .score-wrapper
          margin-bottom 8px
          font-size 0
          .title
            display inline-block
            vertical-align top
            line-height 18px
            font-size 12px
            color rgb(7, 17, 27)
          .star
            display inline-block
            margin 0 12px
            vertical-align top
          .score
            line-height 18px
            display inline-block
            vertical-align top
            font-size 12px
            color rgb(255, 153, 0)

        .delivery-wrapper
          font-size 0
          .title
            line-height 18px
            font-size 12px
            color rgb(7, 17, 27)
          .delivery
            margin-left 12px
            font-size 12px
            color rgb(147, 153, 159)

    .ratings-wrapper
      padding 0 18px
      .ratings-item
        display flex
        padding 18px 0
        border-1px(rgba(7, 17, 27, 0.1))
        .avatar
          flex 0 0 28px
          width 28px
          margin-right 12px
          img
            border-radius 50%
        .content
          flex: 1
          position relative
          .name
            margin-bottom 4px
            line-height 12px
            font-size 10px
            color rgb(7, 17, 27)
          .star-wrapper
            margin-bottom 6px
            font-size 0
            .star
              display inline-block
              vertical-align top
              margin-right 6px
            .delivery
              display inline-block
              vertical-align top
              margin-bottom 4px
              line-height 12px
              font-size 10px
              color rgb(7, 17, 27)

          .text
            margin-bottom 8px
            font-size 12px
            line-height 18px
            color rgb(7, 17, 27)
          .recomand-wrapper
             line-height 16px
             font-size 0
             .icon-thumb_up,.item
               display inline-block
               margin 0 8px 4px 0
               font-size 9px
             .icon-thumb_up
               color rgb(0,160,220)
             .item
               padding 0 6px
               border 1px solid rgba(7,17,27,0.1)
               border-radius 1px
               color rgb(147,153,159)
          .time
            position  absolute
            right 0px
            top 0px
            line-height 12px
            font-size 10px
            color rgb(147,153,159)
</style>




