<template>
  <div id="app">

    <v-header :seller="seller">

    </v-header>

    <div class="tab border-1px">
      <div class="tab-item">
        <!-- 使用 router-link 组件来导航. -->
        <!-- 通过传入 `to` 属性指定链接. -->
        <!-- <router-link> 默认会被渲染成一个 `<a>` 标签 -->
        <router-link to="/goods">商品</router-link>
        <!--<a v-link="{path:'/goods'}"></a>-->
      </div>

      <div class="tab-item">
        <router-link to="/ratings">评论</router-link>
        <!--<a v-link="{path:'/ratings'}">评论</a>-->
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
        <!--<a v-link="{path:'/seller'}">商家</a>-->
      </div>

    </div>
    <!--路由外链-->
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>

<script type="text/ecmascript-6">
  import header from './components/heder/header.vue';
  import {urlParse} from './common/js/util';
  const appAllData = require('../static/data.json');
//  const ERR_OK = 0;
  export default {
    data() {
      return {
        seller: {
          id: (() => {
            let queryParam = urlParse();
            return queryParam.id;
          })()
        }
      };
    },
    created() {
      console.log('2312312312');
      console.log(appAllData.seller);
      this.seller = Object.assign({}, this.seller, appAllData.seller);
      console.log(this.seller);
//      this.$http.get('/api/seller' + '?id=' + '123123').then(response => {
//        if (response.body.errno === ERR_OK) {
//          this.seller = Object.assign({}, this.seller, response.body.data);
//          console.log(this.seller);
//        }
//      }, response => {
//        console.log(response);
//      });
    },
    components: {
      'v-header': header
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "common/stylus/mixin.styl"
  @import "common/stylus/base.styl"
  @import "common/stylus/index.styl"
  #app
    .tab
      display: flex
      width: 100%
      height: 40px
      line-height: 40px
      border-1px(rgba(7, 17, 27, 0.1))
      .tab-item
        flex: 1
        text-align: center
        & > a
          display: block
          font-size: 14px
          color: rgb(77, 85, 93)
          &.active
            color: rgb(240, 20, 20)

</style>



